import Parser from 'html-react-parser';
import Fade from 'react-reveal/Fade';
import React from 'react'

export default class About extends React.Component {
    render () {
      const property = {
          Title:
             'ABOUT',
        paragraphe1:
            'Meta Manager is a next-generation ecosystem integrating a football managing Play To Earn based on data and more utilities for holders in the world and the metaverse. Connected to a mind-blowing VR experience in crowded futuristic stadiums and powered by a stunning NFT collection of 8888 unique characters, our ecosystem is based on the Ethereum Blockchain. ',
        paragraphe2:
            'Collect Meta Players, choose your tactic, manage your squad, recruit the perfect players to improve your Team and make them progress by playing. Only the best managers in the world will be able to compete in the Meta World Cup, one of the most expected event in the web3 community.',
        paragraphe3:
            'Meta Manager also offers a lot of access and rewards to the holders, invitations to sport events worldwide, exclusive access to our metaverse to watch football games directly with the community for free, integrating lucky holders directly in the game with face scanning in our studio and many other rewards to come… IF YOU’RE A PART OF THE COMMUNITY, YOU’RE PART OF OUR TEAM.',
    };
 
    document.addEventListener("scroll", () => {
      if (window.scrollY >  window.innerWidth/500 + 250) {
      var test = document.getElementsByClassName("fade_text");
      var arr = Array.prototype.slice.call(test)
      arr.map((e) => 
        {
          e.style.animation = "fadein 2s";
          e.style.opacity = "1";
          return e;
        });
      }
    })
  
    return (<><section id="about">
    {/* <h1 className="rotate fade_text">
      {Parser(property.Title)}
    </h1> */}
    <div>
      <Fade left>
      <hr id="barre"/>
      </Fade>
      <p className="fade_text rotate1"> 
      {Parser(property.paragraphe1)}
      </p>
      <p className="fade_text">
      {Parser(property.paragraphe2)}  
      </p>
      <p className="fade_text">
      {Parser(property.paragraphe3)}
      </p>
    </div>
  </section></>);
    }
}