export default function cursor () {
    document.addEventListener("scroll", () => {
        var test = document.getElementsByClassName("scroll-downs");
        var arr = Array.prototype.slice.call(test)
        arr.map((e) => 
          {
            e.style.animation = "scrollY 1s";
            // e.style.animation = "filter_ 0.1s";
            e.style.opacity = "0";
            return e;
          });
      })

}